<template>
  <div class="ripples">
    <div v-for="(wave,index) in waves" :key="index" class="wave" :class="{show: wave.display}"></div>
  </div>
</template>

<script>
/* eslint-disable no-debugger */
/* eslint-disable no-console */

export default {
  name: "Ripples",
  components: {},
  props: {
    enabled: {
      type: Boolean,
      value: false
    }
  },
  data: () => {
    return {
      loopHandle: 0,
      currWave: 0,
      waves: [
        { display: false },
        { display: false },
        { display: false },
        { display: false },
        { display: false },
        { display: false },
        { display: false },
        { display: false },
        { display: false },
        { display: false },
        { display: false },
        { display: false }
      ]
    };
  },
  computed: {},
  watch: {
    enabled(val) {
      if (val) {
        const loopHandle = +new Date();
        this.loopHandle = loopHandle;
        setTimeout(() => {
          this.waveLoop(loopHandle);
        }, 600);
      }
    }
  },
  methods: {
    waveLoop(loopHandle) {
      if (!this.enabled || this.loopHandle !== loopHandle) {
        return false;
      }
      const { waves, currWave } = this;
      const nextWave = currWave >= waves.length - 1 ? 0 : currWave + 1;
      const newWaves = [...waves];
      newWaves[currWave].display = true;
      newWaves[nextWave].display = false;
      this.waves = newWaves;
      this.currWave = nextWave;
      setTimeout(() => {
        this.waveLoop(loopHandle);
      }, 6000);
    }
  },
  created() {
    if (this.enabled) {
      const loopHandle = +new Date();
      this.loopHandle = loopHandle;
      setTimeout(() => {
        this.waveLoop(loopHandle);
      }, 600);
    }
  },
  beforeDestroy() {
    this.loopHandle = 0;
  }
};
</script>

<style>
.ripples {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 150px;
  height: 150px;
  pointer-events: none;
}
.ripples > .wave {
  box-sizing: border-box;
  border: 1px solid #fff;
  border-radius: 50%;
  position: absolute;
  left: 0pt;
  top: 0pt;
  margin: 0;
  width: 150px;
  height: 150px;
  opacity: 0;
  display: none;
}
.show.wave {
  display: block;
}
.ripples > .wave {
  animation: ripples 20s 0s ease-out;
  animation-fill-mode: backwards;
  animation-iteration-count: 1;
}
@keyframes ripples {
  0% {
    transform: scale(1);
    opacity: 0;
  }
  0.5% {
    transform: scale(1.01);
    opacity: 0.32;
  }
  100% {
    transform: scale(1.5);
    opacity: 0;
  }
}
</style>
