    <swiper
      v-if="picker"
      @slideChange="changeScene"
      ref="picker"
      :options="{initialSlide: picker.current,loop: true, lazy: true, preloadImages:false}"
    >
      <swiper-slide v-for="scene in picker.scenes" :key="scene.id" class="scene-cover">
        <img
          :data-src="`${scene.cover_url}?imageView2/1/w/720/h/1440`"
          :src="lazied ? '' : `${scene.cover_url}?imageView2/1/w/720/h/1440`"
          class="swiper-lazy"
        >
        <div
          class="scene-mask"
          :style="{background:`linear-gradient(rgba(${scene.scene_masks[0]}), rgba(${scene.scene_masks[1]}))`}"
        ></div>
        <div class="scene-mask-break"></div>
      </swiper-slide>
    </swiper>
    <div class="timer-main">
      <div class="pagination">
        <div class="btn material-icons" @click="prevScene">chevron_left</div>
        <h2 class="scene-name">{{getTR(picker.scenes[picker.current].name)}}</h2>
        <div class="btn material-icons" @click="nextScene">chevron_right</div>
      </div>
      <div class="break-tips">{{breakTip}}</div>
      <div class="duration-container">
        <div class="remain" @click="showDurationsView">
          <span
            class="remain-char"
            v-for="(remainChar,index) in formatRemainTime(timer.remain)"
            :key="`remainChar-${index}`"
          >{{remainChar}}</span>
        </div>
        <span class="break-stage-tips">{{$i18n('timerLongBreakTips')}}</span>
        <ripples :enabled="player.status === 'playing' && !player.muted"></ripples>
        <circle-progress
          class="circle-progress"
          :diameter="150"
          :strokeWidth="3"
          progressColor="#fff"
          backgroundColor="rgba(255,255,255,0.16)"
          :animateSpeed="(timer.duration - timer.remain) ? 500 : 1"
          :totalSteps="timer.duration"
          :completedSteps="timer.duration - timer.remain"
        ></circle-progress>
      </div>
      <div class="actions">
        <div
          v-if="['break','long_break'].includes(timer.stage)"
          class="action-btn start-btn"
          style="color: #66A27C"
          @click="start"
        >{{$i18n('timerStartBreakBtn')}}</div>
        <div
          v-else
          class="action-btn start-btn"
          :style="`color:rgba(${picker.scenes[picker.current].primary_color})`"
          @click="start"
        >{{$i18n('timerStartFocusBtn')}}</div>
        <div class="action-btn primary-skip-btn" @click="skip">{{$i18n('timerSkipBtn')}}</div>
        <div
