          @click="toggleMute"
        >{{player.muted ? 'volume_off' : 'volume_up'}}</div>
        <div
          class="today-records"
          v-if="records.data"
          @click="gotoInfos"
        >{{$i18n('today')}} Â· {{records.data.today_times}}</div>
        <div class="btn material-icons" @click="gotoSettings">settings</div>
      </div>
    </div>
    <div class="durations-view">
      <div class="durations-view-mask"></div>
      <div class="duration-radios">
        <div class="duration-radio" @click="setDuration(5)">
          <div>
            <span class="count">5</span>
            <span class="unit">{{$i18n('mins')}}</span>
          </div>
        </div>
        <div class="duration-radio" @click="setDuration(10)">
          <div>
            <span class="count">10</span>
            <span class="unit">{{$i18n('mins')}}</span>
          </div>
        </div>
        <div class="duration-radio" @click="setDuration(20)">
