            <span class="count">120</span>
            <span class="unit">{{$i18n('mins')}}</span>
          </div>
        </div>
      </div>

      <form novalidate @submit.prevent="setDuration(durationInput)" class="duration-input-form">
        <label class="duration-input">
          <input
            v-model="durationInput"
