          >
          <span>{{$i18n('mins')}}</span>
        </label>
        <button class="confirm-btn" min="1" type="submit">{{$i18n('durationInputConfirmBtn')}}</button>
      </form>
      <div class="material-icons close-btn btn" @click="hideDurationsView">close</div>
    </div>
  </div>
