<script>
/* global chrome */
import "swiper/dist/css/swiper.css";

import { swiper, swiperSlide } from "vue-awesome-swiper";
import Ripples from "@/components/ripples";
import CircleProgress from "@/components/circle-progress";

function convertTime(time) {
  let totalSec = Math.ceil(time / 1000);
  let sec = totalSec % 60;
  let MIN = parseInt(totalSec / 60);
  MIN = MIN < 10 ? `0${MIN}` : MIN;
  sec = sec < 10 ? `0${sec}` : sec;
  return `${MIN}:${sec}`;
}

function randomNum(minNum, maxNum) {
  switch (arguments.length) {
    case 1:
      return parseInt(Math.random() * minNum + 1, 10);
    case 2:
      return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
    default:
      return 0;
  }
}

export default {
  name: "Popup",
  components: {
    swiper,
    swiperSlide,
    Ripples,
    CircleProgress
  },
  data: () => {
    return {
      bgPage: null,
      scenes: null,
      lazied: true,
      timer: {},
      picker: {},
      player: {},
      records: {},
      settings: {},
      breakTip: "",
      durationsViewDisplay: false,
      status: "stop",
      duration: 5000,
      remain: 5000,
      durationInput: ""
    };
  },
  computed: {
    swiper() {
      return this.$refs.picker.swiper;
    }
  },
  watch: {
    "picker.current"(index) {
      this.player.setUrl(this.picker.scenes[index].sound_url);
    },
    "timer.stage"(stage) {
      if (["break", "long_break"].includes(stage)) {
        this.breakTip = this.$i18n(`breakQuotes_${randomNum(0, 5)}`);
      }
    },
    durationInput(val) {
      let newVal = val.replace(/[^\d]/g, "");
      if (val !== newVal) {
        this.durationInput = newVal;
      }
    }
  },
  created() {
    let bgPage = chrome.extension.getBackgroundPage();
    this.timer = bgPage.timer;
    this.picker = bgPage.picker;
    this.player = bgPage.player;
    this.records = bgPage.records;
    this.settings = bgPage.settings;
    function clean() {
      this.timer = null;
      this.picker = null;
      this.player = null;
      this.swiper = null;
      bgPage = null;
      window.popup = null;

      document.body.innerHTML = "";
    }

    addEventListener("unload", clean, true);
    // this.swiper.lazy.load();
    setTimeout(() => {
      this.lazied = false;
    }, 1000);
  },
  methods: {
    formatRemainTime(remain) {
      return convertTime(remain);
    },
    getTR(item) {
      return item[this.$i18n("serverLang")];
    },
    gotoInfos() {
      chrome.tabs.create({
        url: `chrome-extension://${this.$i18n(
          "@@extension_id"
        )}/options.html#/infos`
      });
    },
    gotoSettings() {
      chrome.tabs.create({
        url: `chrome-extension://${this.$i18n(
          "@@extension_id"
        )}/options.html#/settings`
      });
    },

    showDurationsView() {
      if (
        this.timer.status !== "stop" ||
        ["break", "long_break"].includes(this.timer.stage)
      ) {
        return;
      }
      this.durationInput = "";
      this.durationsViewDisplay = true;
    },

    hideDurationsView() {
      this.durationsViewDisplay = false;
    },

    setDuration(min) {
      if (!min || isNaN(min) || min == 0) {
        min = 5;
      } else {
        this.timer.setFocusDuration(min * 60 * 1000);
      }
      this.hideDurationsView();
    },

    start() {
      this.timer.start();
    },
    continuePlay() {
      this.timer.continue();
      if (this.timer.stage === "focus") {
        this.player.play();
      }
    },
    pause() {
      this.timer.pause();
      if (this.timer.stage === "focus") {
        this.player.pause();
      }
    },
    stop() {
      if (this.timer.stage === "focus") {
        this.timer.stop();
      } else {
        this.timer.skip();
      }
      this.player.stop();
    },
    stopBreak() {
      this.timer.stop();
    },
    skip() {
      this.timer.skip();
    },
    nextScene() {
      this.swiper.slideNext();
    },
    prevScene() {
      this.swiper.slidePrev();
    },
    changeScene() {
      this.picker.setIndex(this.swiper.realIndex);
    },
    toggleMute() {
      this.player.muted ? this.player.muteOn() : this.player.muteOff();
    }
  }
};
</script>

